import { ChannelDefinition, PostContent } from '@youfoundation/dotyoucore-js';
import { ChannelDefinitionVm } from '../../../../../hooks/blog/useChannels';

const PostMeta = ({
  dotYouId,
  post,
  channel,
  className,
  size = 'text-xs',
}: {
  dotYouId?: string;
  post: PostContent;
  channel: ChannelDefinitionVm | ChannelDefinition;
  className?: string;
  size?: 'text-xs' | 'text-sm';
}) => {
  const now = new Date();
  const date = new Date(post.dateUnixTime);
  const format: Intl.DateTimeFormatOptions = {
    month: 'short',
    day: 'numeric',
    year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined,
    hour: 'numeric',
    minute: 'numeric',
  };

  return (
    <div className={`flex flex-row ${size} font-medium ${className ?? ''}`}>
      <span>{date.toLocaleDateString(undefined, format)}</span>
      <a
        className="ml-1 border-l pl-1 text-button dark:border-slate-500"
        href={`${dotYouId ? `https://${dotYouId}/` : ''}/home/posts/${channel.slug}`}
      >
        {channel?.name ? `${channel?.name}` : ''}
      </a>
    </div>
  );
};

export default PostMeta;
