import { getChannelDrive, Image as ImageType } from '@youfoundation/dotyoucore-js';
import { useNavigate, useParams } from 'react-router-dom';
import useBlog from '../../../hooks/blog/useBlog';
import Image from '../../../components/Image/Image';
import { useCallback, useEffect, useState } from 'react';
import ActionButton from '../../../components/ui/Buttons/Button';
import PostMeta from '../../../components/Blog/Common/Blocks/Meta/Meta';
import AuthorImage from '../../../components/Blog/Common/Blocks/Author/Image';
import AuthorName from '../../../components/Blog/Common/Blocks/Author/Name';
import LoadingParagraph from '../../../components/ui/LoadingParagraph/LoadingParagraph';

const BlogImageDetail = () => {
  const { channelKey, postKey, attachmentKey } = useParams();
  const [currIndex, setCurrIndex] = useState(attachmentKey ? parseInt(attachmentKey) : 0);
  const [pageWidth, setPageWidth] = useState(0);
  const navigate = useNavigate();
  const { data: blogData } = useBlog(
    channelKey && postKey
      ? {
          channelSlug: channelKey,
          blogSlug: postKey,
        }
      : undefined
  );

  const containerRef = useCallback((node: HTMLDivElement) => {
    if (node !== null) {
      setPageWidth(node.clientWidth);
    }
  }, []);

  const post = blogData?.activeBlog.content;
  const channel = blogData?.activeChannel;
  const imageFileIds =
    (post as ImageType)?.imageFileIds ||
    (post?.primaryImageFileId ? [post.primaryImageFileId] : undefined);

  const doSlide = (dir: 1 | -1) => {
    const newIndex = currIndex + dir;
    if (imageFileIds && newIndex >= imageFileIds.length) {
      setCurrIndex(imageFileIds.length - 1);
      return;
    }
    if (newIndex < 0) {
      setCurrIndex(0);
      return;
    }
    setCurrIndex(newIndex);
  };

  const doClose = () => {
    const paths = window.location.pathname.split('/');
    paths.pop();
    navigate(paths.join('/'));
  };

  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => {
      if (e.key === 'ArrowLeft') {
        doSlide(-1);
      } else if (e.key === 'ArrowRight') {
        doSlide(1);
      }
    };

    window.addEventListener('keydown', handleEsc);

    return () => {
      window.removeEventListener('keydown', handleEsc);
    };
  }, [currIndex]);

  return (
    <div className="relative z-50" role="dialog" aria-modal="true">
      <div className="fixed inset-0 bg-gray-500 opacity-90 transition-opacity"></div>

      <div className="fixed inset-0 z-10 overflow-y-auto">
        <div className="flex h-full flex-row">
          <div className={`relative h-full w-full overflow-hidden`} ref={containerRef}>
            <div
              className={`flex h-full w-[500vw] flex-row ${'transition-transform'} duration-500`}
              style={{
                transform: `translatex(${pageWidth * currIndex * -1}px)`,
              }}
              onClick={doClose}
            >
              {imageFileIds?.map((fileId) => (
                <div
                  className="my-auto max-h-screen w-screen flex-shrink-0 p-5 md:w-[calc(100vw-20rem)]"
                  key={fileId}
                  onClick={(e) => e.stopPropagation()}
                >
                  {!post ? (
                    <LoadingParagraph className="aspect-square h-full max-h-56" />
                  ) : (
                    <Image
                      className={`h-auto max-h-[calc(100vh-5rem)] w-auto max-w-full object-contain`}
                      fileId={fileId}
                      targetDrive={getChannelDrive(post.channelId)}
                      alt="post"
                      fit="contain"
                      previewThumbnail={
                        imageFileIds.length === 1
                          ? blogData?.activeBlog.previewThumbnail
                          : undefined
                      }
                    />
                  )}
                </div>
              ))}
            </div>
            <ActionButton
              icon="times"
              onClick={doClose}
              className="absolute left-2 top-2 rounded-full p-3"
              size="square"
            />
            {currIndex !== 0 ? (
              <ActionButton
                icon="left"
                onClick={() => doSlide(-1)}
                className="absolute left-2 top-[50%] rounded-full p-3"
                size="square"
              />
            ) : null}
            {imageFileIds && currIndex !== imageFileIds.length - 1 ? (
              <ActionButton
                icon="right"
                onClick={() => doSlide(1)}
                className="absolute right-2 top-[50%] rounded-full p-3"
                size="square"
              />
            ) : null}
          </div>

          <div className="pb- hidden w-[20rem] flex-shrink-0 bg-white p-5 md:block">
            <div className="-m-2 flex flex-row border-b">
              <div className="p-2">
                <AuthorImage size="sm" />
              </div>
              <div className="flex max-w-lg flex-grow flex-col p-2">
                <div className="mb-2 text-foreground text-opacity-60">
                  <h2 className="mr-4">
                    <AuthorName />
                  </h2>
                  {post && channel ? <PostMeta channel={channel} post={post} /> : null}
                </div>
                <p>{post?.caption}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogImageDetail;
