import { useQuery } from '@tanstack/react-query';
import { PublicClient } from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

type useBlogProps = {
  channelSlug?: string;
  blogSlug?: string;
};

const useBlog = ({ channelSlug, blogSlug }: useBlogProps = {}) => {
  const { getSharedSecret, getApiType } = useAuth();
  const client = new PublicClient({ api: getApiType(), sharedSecret: getSharedSecret() });

  const fetchBlog = async ({ channelSlug, blogSlug }: useBlogProps) => {
    if (!channelSlug || !blogSlug) {
      return;
    }

    const data = await client.blogPostReadonlyProvider.getPostBySlug(channelSlug, blogSlug);
    if (!data) {
      // No match on a slug, perhaps better luck with the post id:
      const channel = await client.blogPostReadonlyProvider.getChannelDefinitionBySlug(channelSlug);
      if (!channel) return;
      const postFile = await client.blogPostReadonlyProvider.getPost(channel.channelId, blogSlug);
      if (postFile) return { activeBlog: postFile, activeChannel: channel };
      return;
    }

    return { activeBlog: data.postFile, activeChannel: data.channel };
  };

  return useQuery(['blog', blogSlug], () => fetchBlog({ channelSlug, blogSlug }), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
    enabled: !!channelSlug && !!blogSlug,
  });
};

export default useBlog;
